.App {
  text-align: center;
}
.topimg{
  width: 100vw;
  height: 7vh;
  background-color: rgb(173, 173, 228);
  text-align: center;
  padding-top: 1vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
 
}

  .firstimg{ 
    padding-right: 10px;
    border-radius: 50%;
    height: 6vh;
  }
  .content{
    width: 80vw;
    margin-bottom: 5vh;
    background-color: rgb(99, 99, 134);
    text-align: center;
    padding-top:2vh;
    padding-bottom:2vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: 5vh;
    border-radius: 20px;

  }

  .insidecontent{
    padding-top:2vh;
    padding-bottom:2vh;
    width: 70vw;
    background-color: rgb(48, 48, 67);
    border-radius: 20px;
    overflow: hidden;
  }

  input[type="text"]{
    background-color:rgb(249, 249, 249);
    height: 5vh;
    width: 45vw;
    border-radius: 5px;
    border:1px solid white;
    margin-top:4px;
    margin-bottom: 4px;
  }

.contentbutton{
  height: 5vh;
  background-color: rgb(196, 211, 237);
  margin-top:2vh;
  border-radius: 10px;
  border:1px solid white;
  margin-bottom: 4px;
  padding-left: 2vw;
  padding-right: 2vw;
  font-size: 15px;
}
.meme{
  border: 2px solid rgb(137, 137, 137); 
  text-align: center;
  width: 80vw;
  margin:auto;
  overflow-x: hidden;
  border-radius: 20px;

}

.top{
  position: absolute;
  margin-left: 32vw;
 z-index: 1;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #a32424;
}
.bottom{
  position: absolute;
  margin-left: 38vw;
 z-index: 1;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #a32424;
  margin-top:24vh;
}
.imgbox{
  width: 80vw;
  height: 30vh;
 width:fit-content;
}


